	// link
	&.ant-btn-link {
		&.btn-main-primary {
			color: $mainPrimary !important;
			background-color: transparent !important;
			border: 0px;

			&:hover {
				color: $mainPrimary;
			}

			&:focus {
				color: $mainPrimary;
			}
		}

		&.btn-delete {
